<template>
  <div>
    <Header title="新入会员"></Header>
    <div class="member-wrap">
      <router-link
        v-for="(item, index) in list"
        :key="index"
        :to="{ path: '/customer', query: { id: item.id } }"
      >
        <div class="one-member clearfix">
          <div class="right">
            <div class="top">
              <h6 class="name inaline">{{ item.memberName }}</h6>
              <p class="time">{{ item.createTime }}</p>
            </div>
            <p class="number">{{ item.memberNo }}</p>
          </div>
        </div>
      </router-link>
    </div>
  </div>
</template>

<script>
import Header from '@/components/Header1.vue';
import { querynewMember } from '@/api/index';

export default {
  data() {
    return {
      list: []
    };
  },
  components: {
    Header
  },
  created() {
    querynewMember().then(res => {
      if (res) {
        if (res.data.code === 0) {
          this.list = res.data.data;
        } else {
          this.$Toast(res.data.msg);
        }
      }
    });
  }
};
</script>

